
@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
}


html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}
.facebook {
  
  color: #3b5998;
  height: 2em;
  width: 2em;
}

.instagram {
  color: #c32aa3;
  height: 2em;
  width: 2em;
}

.youtube {
  color: #ff0000;
  height: 2em;
  width: 2em;
}


.main-nav{
  width: 100%;
  height: 6rem;
  display: grid;
  grid-template-columns: 10rem 1fr 2fr 1fr 10rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;

}
.logo
{
  display: grid;
  justify-content: start;
  align-items: center;
  grid-column: 2/3;
}
.logo h2 {
  font-size: 2.5rem;
  font-weight: 400;
  text-transform: uppercase;
  background: -webkit-linear-gradient(#eee, rgb(69, 57, 248));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}



.menu-link{
  grid-column: 3/4;

}

.menu-link ul{
  height: 6rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  
}
.menu-link ul li {
  font-size: 1.8rem;
}

.menu-link ul li a {
  text-transform: capitalize;
  color: rgba(0, 0, 3, 0.8);
  transition: 0.5s;
}
.menu-link ul li:hover > a {
  transform-origin: left;
  color: rgba(0, 0, 3, 1);
  text-decoration: underline;
  transition: 0.5s;
}

.social-media{
  grid-column:4/5;
  text-align: end;
  
}
.social-media .hamburger-menu{
  display: none;
}

.social-media ul{
  height: 6rem;
  display: grid;
  grid-template-columns: 3fr repeat(3, 1fr);
  align-items: center;
  list-style: none;
}

.social-media ul li:first-child {
grid-column: 2/3;

}

.social-media-desktop li {
  justify-content: center;
}


.hamburger-menu{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.plan-menu
{
  align-items: center;
  margin-left: 50px;
  margin-right: 50px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}



/*responsive css style*/

@media (max-width: 1080px)
{
  .main-nav{
    height: 6px;
    grid-template-columns:2rem 3fr 3fr 1fr 2rem ;
    
  }
  .logo,
  .menu-link ul,
  .social-media ul {
    height: 8rem;
  }

  .plan-menu{
    display: flex;
    align-items:center;
    justify-content: space-around;
  }
}




@media (max-width: 998px)
{
  .main-nav{
    height: 7rem;
    grid-template-columns:2rem 2fr 3fr 2rem 2rem ;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .menu-link{
    display:none;
  }
  .logo,
  .social-media ul {
    height: 7rem;
  }
  .social-media .social-media-desktop{
    height: 0;
    display: none;
  }
  .social-media .hamburger-menu{
    display: block;
    font-size: 2rem
  }
  .mobile-menu-link{
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }
  .mobile-menu-link{
    background-color: white;
    height: 20rem;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    padding-left: 3rem;
    transition: all 2s linear;
    transform-origin: top;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .mobile-menu-link ul {
    height: 20rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: start;
    
  }
  .social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }

  .plan-menu{
  display: flex;
  align-items:center;
  justify-content: space-around;
}
}


@media (max-width: 798px) {
  .main-nav {
    height: 5rem;
    grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .logo,
  .social-media ul {
    height: 5rem;
  }

  .social-media {
    height: 5rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}


